@import url(./fonts/stylesheet.css);

* {
    margin: 0;
    padding: 0;

    font-size: 14px;
    font-weight: 500;
    font-family: 'Open Sans';

    user-select: none;
}

body {
    width: 100vw;
    height: 100vh;

    overflow: hidden;
}
#root {
    width: 100%;
    height: 100%;

/*    background: url(https://steamuserimages-a.akamaihd.net/ugc/405682610614540575/DF1BB078BA8227EB1166902A7C47130B8131F91D/?imw=512&amp;imh=288&amp;ima=fit&amp;impolicy=Letterbox&amp;imcolor=%23000000&amp;letterbox=true);*/
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    position: relative;
}

input, button, textarea {
    outline: none;
}

img {
    width: 75px;
    height: auto;
}


::-webkit-scrollbar {
    width: 3px;
    height: 2px;

    background-color: transparent;
}
::-webkit-scrollbar-thumb {
    background-color: #c7c5c2;
    border-radius: 9em;
    box-shadow: inset 1px 1px 10px #c7c5c2;
}


.btn {
    border: 1px solid white;
    background-color: transparent;

    color: white;
    padding: 5px 13px;

    border-radius: 3px;

    cursor: pointer;
    transition: .3s;

    display: flex;
    align-items: center;
    justify-content: center;
}
.btn:hover {
    border-color: #48b0bb;
    background-color: #48b0bb;
}

.btn svg:first-child {
    margin-right: 5px;
}


input {
    padding: 12px;
    border: 0;

    background-color: rgba(255, 255, 255, .2);
    color: white;

    border-radius: 3px;
    font-size: 13px;
}
input::placeholder {
    color: white;
    opacity: .4;

    font-size: 13px;
}


.avatar {
    border-radius: 50%;
    overflow: hidden;

    position: relative;
}
.avatar div {
    display: flex;
    align-items: center;
    justify-content: center;
}
.avatar {
    min-width: 60px;

    width: 60px;
    height: 60px;
}
.avatar-min {
    min-width: 30px;

    width: 30px;
    height: 30px;
}
.avatar-big {
    min-width: 100px;

    width: 100px;
    height: 100px;
}
.avatar img,
.avatar div {
    width: 100%;
    height: auto;
}


input[type="checkbox"] {
    appearance: none;

    width: 16px;
    height: 16px;

    padding: 0;

    background-color: rgba(255, 255, 255, .2);
    border-radius: 3px;

    cursor: pointer;
    position: relative;
}
input[type="checkbox"]:before {
    position: absolute;

    display: block;
    content: "";

    width: 75%;
    height: 75%;

    background-color: silver;
    border-radius: 3px;

    transition: .2s;

    top: 50%;
    left: 50%;

    transform: translate(-50%, -50%);

    opacity: 0;
}
input[type="checkbox"]:checked:before {
    opacity: 1;
}


.btn-key {
    min-width: calc(25px - 12px);

    display: flex;
    align-items: center;
    justify-content: center;

    padding: 2px 6px;
    border-radius: 4px;

    margin: 0 2px;

    background-color: white;
    color: black;

    border: 0;
}
.btn-key-black {
    background-color: black;
    color: white;
}
.btn-key-other {
    padding: 4px 8px;
}
.btn-key-other img {
    width: 15px;
    height: 15px;
}


input[type="range"]
{
    -webkit-appearance: none;

    border-radius: 0;
    background-color: transparent;
    padding: 0;
}
input[type="range"]::-webkit-slider-runnable-track,
input[type="range"]::-webkit-range-track
{
    width: 100%;
    height: 10px;
    cursor: pointer;
    background: rgba(255, 255, 255, .3);
    border-radius: 7px;

    transition: .2s;
}
input[type="range"]::-webkit-slider-thumb,
input[type="range"]::-webkit-range-thumb,
input[type="range"]::-webkit-thumb
{
    background: #FFFFFF;
    box-shadow: -3px 0px 11px rgba(0, 0, 0, 0.55);

    height: 15px;
    width: 15px;

    border-radius: 50%;

    -webkit-appearance: none;
    margin-top: -3px;

    transition: .2s;
}