.menu {
	position: absolute;

	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	backdrop-filter: blur(5px);
	color: white;

	display: flex;
	align-items: center;
	justify-content: center;
}


.menu .menu-wrap {
	width: 80%;
	height: calc(100% - 100px);

	padding: 50px 0;
}

.menu .menu-wrap .menu-header {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.menu .menu-wrap .menu-header .menu-header-title .menu-header-title-nav {
	display: flex;
	align-items: center;
}
.menu .menu-wrap .menu-header .menu-header-title .menu-header-title-nav section {
	display: flex;
	align-items: center;

	padding: 5px 8px;
	border-radius: 4px;

	cursor: pointer;
	transition: .2s;
}
.menu .menu-wrap .menu-header .menu-header-title .menu-header-title-nav section + section {
	margin-left: 15px;
}
.menu .menu-wrap .menu-header .menu-header-title .menu-header-title-nav section svg {
	font-size: 27px;
	color: #c4c7c6;

	transition: .2s;
}
.menu .menu-wrap .menu-header .menu-header-title .menu-header-title-nav section span {
	font-size: 14px;
	color: #c4c7c6;

	margin-left: 7px;
	transition: .2s;

	width: 0;
	margin: 0;

	opacity: 0;
	visibility: hidden;
}
.menu .menu-wrap .menu-header .menu-header-title .menu-header-title-nav section.menu-header-title-nav-sel {
	background-color: #2eb7cd;
}
.menu .menu-wrap .menu-header .menu-header-title .menu-header-title-nav section.menu-header-title-nav-block {
	opacity: .3;
	cursor: not-allowed;
}
.menu .menu-wrap .menu-header .menu-header-title .menu-header-title-nav section.menu-header-title-nav-sel svg,
.menu .menu-wrap .menu-header .menu-header-title .menu-header-title-nav section.menu-header-title-nav-sel span {
	color: white;
}
.menu .menu-wrap .menu-header .menu-header-title .menu-header-title-nav section.menu-header-title-nav-sel span {
	width: auto;
	margin-left: 10px;

	opacity: 1;
	visibility: visible;
}

.menu .menu-wrap .menu-header .menu-header-char {
	display: flex;
	align-items: center;
}
.menu .menu-wrap .menu-header .menu-header-char .menu-header-char-info {
	text-align: right;
	margin-right: 15px;
}
.menu .menu-wrap .menu-header .menu-header-char .menu-header-char-info h1 {
	font-size: 18px;
	text-transform: uppercase;
}
.menu .menu-wrap .menu-header .menu-header-char .menu-header-char-info h2 {
	opacity: .7;
	font-size: 13px;
}
.menu .menu-wrap .menu-header .menu-header-char .menu-header-char-info h3 {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-end;

	margin-top: 10px;
}
.menu .menu-wrap .menu-header .menu-header-char .menu-header-char-info h3 span {
	width: 100%;

	display: flex;
	align-items: center;
	justify-content: flex-end;
}
.menu .menu-wrap .menu-header .menu-header-char .menu-header-char-info h3 span svg {
	transform: translateY(1px);
	margin-right: 5px;
}


.menu .menu-body {
	height: calc(100% - 100px);
	margin-top: 25px;
}
.menu .menu-body-choice {
	display: flex;
	align-items: flex-start;
}

.menu .menu-body .menu-body-nav {
	border-right: 1px solid #d1d4d673;
	padding-right: 35px;
}
.menu .menu-body .menu-body-nav h1 {
	font-size: 19px;

	font-family: 'Chalet';
	text-transform: uppercase;

	color: #d1d4d6;

	cursor: pointer;
	transition: .2s;

	white-space: nowrap;
}
.menu .menu-body .menu-body-nav .menu-body-nav-block {
	opacity: .2;
	cursor: not-allowed;
}
.menu .menu-body .menu-body-nav h1.menu-body-nav-sel {
	color: #2eb7cd;
	font-weight: 600;
}
.menu .menu-body .menu-body-nav h1 + h1 {
	margin-top: 15px;
}

.menu .menu-body .menu-body-wrap {
	width: 100%;
	height: 100%;

	overflow: auto;
}
.menu .menu-body-choice .menu-body-wrap {
	margin-left: 35px;
}
.menu .menu-body .menu-body-wrap::-webkit-scrollbar {
	display: none;
}


.menu .menu-body .menu-body-notfound {
	width: 100%;
	margin-top: 7%;

	display: flex;
	justify-content: center;
	flex-wrap: wrap;
}
.menu .menu-body .menu-body-notfound span {
	font-size: 65px;
	font-family: 'PriceDown';

	opacity: .6;
}
.menu .menu-body .menu-body-notfound span {
	width: 100%;
	text-align: center;
}
.menu .menu-body .menu-body-notfound .btn {
	margin-top: 10px;
	padding: 7px 23px;

	font-size: 16px;
}